.home__home-page {
    color: #fff; 
    padding: 10px 0px;
    /* background: url('/images/homebg2.jpg') center center/cover no-repeat;  */
}


.home__home-row {
   align-items: center; 
}

.row {
    display: flex;
    margin-right: -15px;
    margin-bottom: -15px;
    margin-left: -15px;
    flex-wrap: wrap;
    align-content: center;
    padding-top: 0px;
}

.col {
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    flex: 1;
    max-width: 50%;
    flex-basis: 50%;

}

.home__home-text-wrapper {
    max-width: 540px;
    padding-top: 0;
    padding-bottom: 60px;
}

.top-line {
    color: #000000;
    font-size: 50 px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-transform: none;
    margin-bottom: 16px;
    
}

.heading {
    margin-bottom: 24px;
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    color: #f7f8fa;
    text-align: left;
    padding-left: 0px;
    padding-right: 50px;
}


.dark {
    color: #1c2237;   
} 

.darkBg {
    background-color: #1c2237;
} 

.home__home-subtitle {
    max-width: 440px;
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
}

.home__home-img-wrapper {
   max-width: 555px; 
}

.home__home-img {
    max-width: 95%;
    padding-right: 0;
    margin: 0 0 0 10px;
}

img {
    border: 0;
    max-width: 100%;
    vertical-align: middle;
    display: inline-block;
}

@media screen and (max-width: 991px) {
    .container {
        padding-right: 30px;
        padding-left: 30px;       
    }
}

@media screen and (max-width: 768px) {
    .home__home-text-wrapper {
        padding-bottom: 65px;       
    }

    .col {
        max-width: 100%;
        flex-basis: 100%;
    }
}