.contact-page {
    padding-top: 10rem;
}

.contact-page-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    height: 50vh;
    font-size: 16px;
  }

  .contact-page-container h3 {
      font-size: 50px;
  }

  .contact-mail {
    font-size: 40px;
    color: black;
    text-decoration: none;
  }

  .contact-phone {
    font-size: 40px;
    color: black;
    text-decoration: none;
    font-style: italic;
  }

 /*  @media only screen and (min-width: 1024px) {
    .contact-page-container {
      display: flex;
    }
  } */

  @media screen and (max-width: 991px) {
    .contact-page-container h3 {
        padding-right: 50px;
        padding-left: 50px;
        font-size: 40px;
    }

    .contact-mail {
        padding-right: 30px;
        padding-left: 30px;  
        font-size: 30px;     
    }

    .contact-phone {
        padding-right: 30px;
        padding-left: 30px; 
        font-size: 30px;      
    }
}

@media screen and (max-width: 768px) {
    .contact-page-container h3 {
        padding-right: 50px;
        padding-left: 50px;
        font-size: 40px;
    }

    .contact-mail {
        padding-right: 50px;
        padding-left: 50px; 
        font-size: 30px;     
    }

    .contact-phone {
        padding-right: 50px;
        padding-left: 50px;
        font-size: 30px;       
    }
}